@use "./vars" as *;
@use "./mixins" as *;
.hamburger_cont {
  position: absolute;
  width: 100vw;
  height: 100vh;
  background-color: $VeryDarkBlue;
  opacity: 75%;
  top: 0;
  left: 0;
  z-index: 10;
}
.hamburger_links_cont {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 80vh;
  align-items: center;
  padding: 30px 40px;
}
.hamburger_links {
  display: flex;
  flex-direction: column;
  align-items: center;

  a {
    text-decoration: none;
    border-top: 1px solid $GrayishBlue;
    border-bottom: 1px solid $GrayishBlue;
    padding: 20px 0;
    color: white;
    text-transform: uppercase;
  }
}
#login {
  text-decoration: none;
  padding: 20px 0;
  color: white;
  text-transform: uppercase;
  border: 2px solid white;
  border-radius: 4px;
}
.ham_images {
  display: flex;
  justify-content: space-between;
  color: white;
  width: 300px;
  margin-bottom: 40px;
  img {
    width: 17px;
    height: 17px;
    cursor: pointer;
  }
}
.ham_links {
  display: flex;
  flex-direction: column;
  text-align: center;
  width: 300px;
}

.hamburger_media {
  display: flex;
  justify-content: space-between;
  width: 90px;
  img {
    cursor: pointer;
  }
}
