$breack-points-up: (
  "medium": "400px",
  "large": "1200px",
);
$breack-points-down: (
  "small": "399px",
  "medium": "1199px",
);

@mixin breack_pint_up($size) {
  @media (min-width: map-get($breack-points-up,$size)) {
    @content;
  }
}
@mixin breack_pint_down($size) {
  @media (max-width: map-get($breack-points-down,$size)) {
    @content;
  }
}
