@use "./mixins" as *;
@use "./vars" as *;
.header_cont {
  display: flex;
  justify-content: space-between;
  padding: 30px 30px 0 30px;
  margin-bottom: 72px;
  @include breack_pint_up(medium) {
    padding: 56px 172px 0 172px;
    margin-bottom: 110px;
  }
}
.ham_icon {
  cursor: pointer;
  height: 16px;
  width: 14px;
}
.main_logo {
  height: 23px;
  @include breack_pint_up(medium) {
    height: 25px;
  }
}
.header_links_medium {
  :not(:last-child) {
    font-size: 16px;
    color: $VeryDarkBlue;
    text-decoration: none;
    margin-right: 45px;
    &:hover {
      color: $PRSoftRed;
      transition: 300ms ease-in-out;
    }
  }
  :last-child {
    background-color: $PRSoftRed;
    border-radius: 5px;
    padding: 16px 33px;
    color: white;
    text-decoration: none;
    border: 1px solid $PRSoftRed;

    &:hover {
      background-color: white;
      border: 1px solid $PRSoftRed;
      color: $PRSoftRed;
      transition: 300ms ease-in-out;
    }
  }
}
