@use "./vars" as *;
@use "./mixins" as *;

.footer_cont {
  overflow: hidden;
}
.footer_btns {
  width: 100%;
  @include breack-pint-up(medium) {
    display: flex;
    align-items: center;
    gap: 20px;
    width: 50%;
  }
}
.footer_first {
  background-color: $PRSoftBlue;
  padding: 70px 30px 50px 30px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
  @include breack-pint-up(medium) {
  }
  h2 {
    font-size: 10px;
    color: white;
    margin-bottom: 16px;
  }
  h1 {
    font-size: 17px;
    color: white;
    margin-bottom: 30px;
  }
  input {
    width: 100%;
    padding: 16px 0;
    border-radius: 5px;
    border: none;
    margin-bottom: 13px;
    @include breack-pint-up(medium) {
      margin-bottom: 0;
    }
  }
  button {
    background-color: $PRSoftRed;
    border: none;
    color: white;
    padding: 16px 0;
    width: 100%;
    border-radius: 5px;
  }
}
.footer_links {
  @include breack-pint-up(medium) {
    display: flex;
    align-items: center;
    gap: 30px;
  }
}
.footer_second {
  background-color: $VeryDarkBlue;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 36px;
  gap: 34px;
  @include breack-pint-up(medium) {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    gap: 10px;
  }
  a {
    color: white;
    text-decoration: none;
  }
}
.footer_media {
  display: flex;
  gap: 36px;
}
