@use "./scss/vars" as *;
@use "./scss/mixins" as *;
@use "./scss/hamburger" as *;
@use "./scss/header.scss" as *;
@use "./scss/content" as *;
@use "./scss/footer" as *;
@import url("https://fonts.googleapis.com/css2?family=Rubik:wght@400;500&display=swap");
* {
  font-family: "Rubik", sans-serif;
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

.DN_for_small {
  @include breack_pint_down(small) {
    display: none;
  }
}
.DN_for_medium {
  @include breack_pint_up(medium) {
    display: none;
  }
}
