@use "./mixins" as *;
@use "./vars" as *;
.App {
  overflow: hidden;
}
.first_cont {
  @include breack_pint_up(medium) {
    display: flex;
    flex-direction: row-reverse;
    justify-content: space-between;
    width: 100%;
    margin-bottom: 190px;
  }
}
.overflow_hiden {
  overflow: hidden;
}
.hero_cont {
  width: 1000px;
}
.hero {
  position: relative;
  height: 220px;
  width: 400px;
  img {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    width: 270px;
    height: 180px;
    z-index: 1;
    @include breack_pint_up(medium) {
      min-height: 385px;
      min-width: 550px;
      // position: static;
      left: 0;
      transform: translateX(0);
    }
  }
}

.hero_back {
  position: absolute;
  width: 500px;
  height: 176px;
  background-color: $PRSoftBlue;
  border-radius: 0 0 0 88px;
  top: 0%;
  left: 66%;
  transform: translate(-25%, 25%);
  @include breack_pint_up(medium) {
    height: 330px;
    width: 800px;
    border-radius: 0 0 0 188px;
    transform: translate(-25%, 15%);
  }
}
.first_text {
  padding: 0 42px;
  @include breack_pint_up(medium) {
    padding: 0 110px;
  }
  h1 {
    text-align: center;
    color: $VeryDarkBlue;
    font-size: 21px;
    margin-top: 48px;
    margin-bottom: 23px;
    @include breack_pint_up(medium) {
      margin: 0;
      text-align: left;
      font-size: 36px;
    }
  }
  p {
    text-align: center;
    color: $GrayishBlue;
    margin-bottom: 32px;
    @include breack_pint_up(medium) {
      text-align: left;
      font-size: 16px;
    }
  }
}
.first_BTN_cont {
  display: flex;
  justify-content: center;
  @include breack_pint_up(medium) {
    display: flex;
    justify-content: flex-start;
  }
}
.get_chrom {
  color: white;
  background-color: $PRSoftBlue;
  font-size: 12px;
  border: none;
  padding: 16px;
  border-radius: 7px;
  cursor: pointer;
  margin-right: 12px;
}
.get_fire {
  color: $VeryDarkBlue;
  font-size: 12px;
  border: none;
  padding: 16px;
  border-radius: 7px;
  cursor: pointer;
}
/////////////// second

.second_cont {
  margin-top: 130px;
  padding: 0 32px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.flex_colom {
}
.second_text {
  max-width: 540px;
  h1 {
    text-align: center;
    font-size: 16px;
    color: $VeryDarkBlue;
    margin-bottom: 20px;
    @include breack_pint_up(medium) {
      font-size: 26px;
      margin-bottom: 26px;
    }
  }
  p {
    font-size: 14px;
    color: $GrayishBlue;
    text-align: center;
    margin-bottom: 40px;
    @include breack_pint_up(medium) {
      font-size: 20px;
      margin-bottom: 75px;
    }
  }
}
.second_links {
  display: flex;
  flex-direction: column;
  @include breack_pint_up(medium) {
    flex-direction: row;
    border-bottom: 1px $GrayishBlue solid;
  }
  :not(:last-child) {
    border-top: 1px $GrayishBlue solid;
    @include breack_pint_up(medium) {
      margin-right: 75px;
      border-top: none;
    }
  }
  :last-child {
    @include breack_pint_up(medium) {
      border-top: none;
    }
    border-top: 1px $GrayishBlue solid;
  }
  margin-bottom: 62px;
}
.second_link_BTN {
  position: relative;
  background: none;
  border: none;
  padding: 24px 0;
  cursor: pointer;
}
.second_active {
  &::before {
    content: "";
    position: absolute;
    top: 63px;
    left: 50%;
    transform: translateX(-50%);
    width: 125px;
    height: 3px;
    background: red;
  }
}
.second_content_cont {
  @include breack_pint_up(medium) {
    display: flex;
    justify-content: space-between;
    margin-top: 50px;
    width: 100%;
    gap: 120px;
  }
}
.second_content_img {
  position: relative;
  height: 220px;
  img {
    position: absolute;
    width: 270px;
    left: 50%;
    transform: translateX(-50%);
    z-index: 2;
    @include breack_pint_up(medium) {
      position: static;
      left: 0;
      transform: translateX(0);
      width: auto;
      max-width: 540px;
      z-index: 2;
      max-width: 530px;
    }
  }
}
.secon_baner_back {
  position: absolute;
  width: 500px;
  height: 176px;
  background-color: $PRSoftBlue;
  border-radius: 0 0 88px 0;
  top: 0%;
  right: -20%;
  transform: translate(-25%, 25%);
  @include breack_pint_up(medium) {
    height: 350px;
    width: 1000px;
    border-radius: 0 0 188px 0;
    z-index: -1;
    transform: translate(-25%, 25%);
  }
}
.second_content_text {
  margin-top: 75px;
  @include breack_pint_up(medium) {
    margin: 0;
    padding-top: 70px;
    max-width: 430px;
  }
  h1 {
    font-size: 16px;
    text-align: center;
    color: $VeryDarkBlue;
    margin-bottom: 20px;
    @include breack_pint_up(medium) {
      font-size: 26px;
      text-align: left;
    }
  }
  p {
    font-size: 12px;
    color: $GrayishBlue;
    text-align: center;
    @include breack_pint_up(medium) {
      font-size: 16px;
      text-align: left;
    }
  }
}
////// third

.third_cont {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 135px;
  padding: 0 40px;
  @include breack_pint_up(medium) {
    margin-top: 250px;
  }
  button {
    background-color: $PRSoftBlue;
    border: none;
    padding: 15px 30px;
    border-radius: 10px;
    color: white;
    cursor: pointer;
  }
}
.third_title {
  @include breack_pint_up(medium) {
    max-width: 540px;
  }
  h1 {
    font-size: 16px;
    color: $VeryDarkBlue;
    text-align: center;
    @include breack_pint_up(medium) {
      font-size: 26px;
      margin-bottom: 30px;
    }
  }
  p {
    font-size: 12px;
    color: $GrayishBlue;
    text-align: center;
    @include breack_pint_up(medium) {
      font-size: 20px;
    }
  }
}
.third_img_cont {
  text-align: center;
  @include breack_pint_up(medium) {
    display: flex;
    gap: 50px;
  }
}
.second_chl {
  margin-top: 40px;
  padding: 40px 0 20px 0;
  box-shadow: 0px 0px 10px $GrayishBlue;
  max-width: 240px;
  border-radius: 10px;
  @include breack_pint_up(medium) {
    margin-top: 90px;
    height: fit-content;
    max-width: 270px;
  }
}
.first_chl {
  margin-top: 40px;
  padding: 40px 0 20px 0;
  box-shadow: 0px 0px 10px $GrayishBlue;
  max-width: 240px;
  border-radius: 10px;
  @include breack_pint_up(medium) {
    margin-top: 50px;
    height: fit-content;
    max-width: 270px;
  }
}
.third_chl {
  margin-top: 40px;
  padding: 40px 0 20px 0;
  box-shadow: 0px 0px 10px $GrayishBlue;
  max-width: 240px;
  border-radius: 10px;
  @include breack_pint_up(medium) {
    margin-top: 130px;
    height: fit-content;
    max-width: 270px;
  }
}
.third_img {
  h2 {
    columns: $VeryDarkBlue;
    font-size: 14px;
    margin: 30px 0 15px 0;
  }
  p {
    color: $GrayishBlue;
    font-size: 11px;
    margin-bottom: 30px;
  }
}
.dot_img {
  width: 100%;
  margin-bottom: 20px;
}
.forth_cont {
  margin-top: 125px;
  margin-bottom: 100px;
  padding: 0 33px;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.forth_text_cont {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  h1 {
    font-size: 20px;
    color: $VeryDarkBlue;
    margin-bottom: 24px;
  }
  p {
    color: $GrayishBlue;
    font-size: 12px;
    margin-bottom: 70px;
  }
}

.question {
  margin-top: 25px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.questions_cont {
  padding-bottom: 20px;
  border-bottom: 1px solid $GrayishBlue;
  @include breack-pint-up(medium) {
    width: 400px;
    max-width: 530px;
  }
}
.display_none {
  display: none;
}
.display_block {
  display: block;
}
.answer {
  margin-top: 34px;
  p {
    color: $GrayishBlue;
    font-size: 12px;
  }
}
.arrow_img {
  max-width: 15px;
}
.more_info {
  margin-top: 40px;
  background-color: $PRSoftBlue;
  border: none;
  color: white;
  padding: 14px 19px;
  border-radius: 4px;
}
